









































































import { Component, Vue } from "vue-property-decorator";
import api from "@/microApps/inspection/api";

@Component({})
export default class CodeManageList extends Vue {
  codesName: String = "";
  codesData: Array<Object> = [];
  codesProp: Object = {
    children: "children",
    label: "catalogName"
  };
  codesListData: Array<Object> = [];
  loading: Boolean = false;
  codePreview: Boolean = false;
  singleCodeVisible: Boolean = false;
  codeImgSrc: String = "";
  catalogInfo: any = {};
  codeMode: any = {};

  addNodeFormVisible: boolean = false;
  nodeForm: any = {};

  handleNodeClick(item: any) {
    this.loading = true;
    this.catalogInfo = item;
    this.$http.post(api.getCodesListByCatalog + item.id).then((res: any) => {
      if (res.data.status) {
        this.codesListData = res.data.data;
        this.codesName = item.catalogName;
      } else {
        this.$message.error(res.data.message);
      }
      this.loading = false;
    });
  }
  previewQRCode(item: any) {
    this.codePreview = true;
    this.codeImgSrc = api.previewCode + item.id;
  }
  handleCodeView(item: any) {
    this.codeMode.codeId = item.id;
    this.codeMode.mode = "view";
    this.codeMode.catalogId = this.catalogInfo.id;
    this.singleCodeVisible = true;
  }
  handleCodeEdit(item: any) {
    this.codeMode.codeId = item.id;
    this.codeMode.mode = "edit";
    this.codeMode.catalogId = this.catalogInfo.id;
    this.singleCodeVisible = true;
  }
  handleCodeDelete(item: any) {
    this.$http.get(api.deleteCode + item.id).then((res: any) => {
      if (res.data.status) {
        this.$message.success("删除成功！");
        this.handleNodeClick(this.catalogInfo);
      } else {
        this.$message.error(res.data.message);
      }
    });
  }
  addACode() {
    this.$router.push({
      name: "codeManageDetail",
      params: {
        preData: JSON.stringify({
          codeId: 0,
          mode: "add",
          catalogId: this.catalogInfo.id
        })
      }
    });
  }
  getInspectQRCodesList() {
    const start = this;
    this.$http.get(api.getInspectQRCodes).then((res: any) => {
      if (res.data.status) {
        start.codesData = res.data.data.children;
        start.handleNodeClick(this.codesData[0]);
      } else {
        start.$message.error(res.data.message);
      }
    });
  }
  changeSingleCodeFormVisible() {
    this.getInspectQRCodesList();
    this.singleCodeVisible = false;
  }

  mounted() {
    this.getInspectQRCodesList();
  }

  openNodeForm(mode: string = "add", data: any = null) {
    this.addNodeFormVisible = true;
    if (data) {
      this.nodeForm = data;
    } else {
      this.nodeForm = {
        catalogType: "INSPECT_PlAN_CODE",
        parentId: null,
        parentName: null
      };
    }
  }
  removeTreeNode(ndoe: any, data: any) {}
  // 保存目录节点
  saveNode() {
    this.$http.post(api.addTemplateNode, this.nodeForm);
    // try {
    //   let {
    //     data: { data, status, message }
    //   } = await Http.post(Url.inspect.addTemplateNode, this.nodeForm);
    //   if (status) {
    //     this.$message.success("添加成功!");
    //   } else {
    //     this.$message.error(message);
    //   }
    // } catch (e) {
    //   return null;
    // }
  }
}
